html {
    height: -webkit-fill-available;
}

body {
    margin: 0;
    font-family: "Roboto", sans-serif;
    height: -webkit-fill-available;
}

h1 {
    font-size: 3rem;
    margin: 0;
}

h2 {
    font-size: 2.5rem;
    margin: 0;
}

h3 {
    font-size: 2rem;
    margin: 0;
}

h4 {
    font-size: 1.5rem;
    margin: 0;
}

h5 {
    font-size: 1.25rem;
    margin: 0;
}

h6 {
    font-size: 1rem;
    margin: 0;
}

/* Fix Mobile Vertical Height */

#root {
    height: -webkit-fill-available;
}
